.display-wrapper {
	display: flex;
	min-height: 100px;
	width: 100%;
	justify-content: center;
	align-items: center;
	margin-top: 5rem;
}
#weather-wrapper {
	width: 80%;
	max-width: 600px;
	/* border: 5px solid coral; */
	border-radius: 5px;
	padding: 10px 20px;
	background-color: #2a2a2a;
}
.location-box {
	font-size: 3rem;
	font-weight: 400;
}
.weather-info {
	padding-top: 1rem;
}
.temp {
	font-size: 2rem;
	color: #ff6127;
	font-weight: 300;
}
hr {
	background-color: white;
}
