.search-wrapper {
	display: flex;
	width: 100%;
	justify-content: center;
	align-items: center;
	margin-top: 3rem;
	/* border: 5px solid coral; */
}
.search-wrapper .search-form {
	width: 80%;
	max-width: 600px;
}
.search-wrapper .search-term {
	display: block;
	width: 100%;
	outline: none;
	height: 3rem;
	font-family: inherit;
	font-size: 1.25rem;
	padding: 5px;
	padding-left: 20px;
	border-radius: 5px;
	border-style: none;
	transition: all 300ms ease;
	box-shadow: 0 0 30px 5px #2b2b2b;
}
.search-term:focus {
	box-shadow: 0 0 1px 1px #ff6127;
}
