@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;700&display=swap);
.search-wrapper {
	display: -webkit-flex;
	display: flex;
	width: 100%;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	margin-top: 3rem;
	/* border: 5px solid coral; */
}
.search-wrapper .search-form {
	width: 80%;
	max-width: 600px;
}
.search-wrapper .search-term {
	display: block;
	width: 100%;
	outline: none;
	height: 3rem;
	font-family: inherit;
	font-size: 1.25rem;
	padding: 5px;
	padding-left: 20px;
	border-radius: 5px;
	border-style: none;
	transition: all 300ms ease;
	box-shadow: 0 0 30px 5px #2b2b2b;
}
.search-term:focus {
	box-shadow: 0 0 1px 1px #ff6127;
}

.display-wrapper {
	display: -webkit-flex;
	display: flex;
	min-height: 100px;
	width: 100%;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	margin-top: 5rem;
}
#weather-wrapper {
	width: 80%;
	max-width: 600px;
	/* border: 5px solid coral; */
	border-radius: 5px;
	padding: 10px 20px;
	background-color: #2a2a2a;
}
.location-box {
	font-size: 3rem;
	font-weight: 400;
}
.weather-info {
	padding-top: 1rem;
}
.temp {
	font-size: 2rem;
	color: #ff6127;
	font-weight: 300;
}
hr {
	background-color: white;
}

