header {
	padding-top: 2rem;
	text-align: center;
	margin: 0;
	font-size: 4rem;
	font-weight: 300;
	text-align: center;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
body {
	min-height: 100vh;
	font-size: 1rem;
	font-family: Poppins, sans-serif;
	background-color: #3b3b3b;
	color: #fff;
}

